import {navigateTo} from "../../lib/js/GlobalFunction.js"
export default {
	name:'rate',
	data() {
		return {
			
		}
			
	},
	props:['rateNum'],
	computed:{
		starNum(){
			switch (this.rateNum) {
				case 0:
					var star = [0,0,5];
					break;
				case 0.5:
					var star = [0,1,4];
					break;
				case 1:
					var star = [1,0,4];
					break;
				case 1.5:
					var star = [1,1,3];
					break;
				case 2:
					var star = [2,0,3];
					break;
				case 2.5:
					var star = [2,1,2];
					break;
				case 3:
					var star = [3,0,2];
					break;
				case 3.5:
					var star = [3,1,1];
					break;
				case 4:
					var star = [4,0,1];
					break;
				case 4.5:
					var star = [4,1,0];
					break;
				case 5:
					var star = [5,0,0];
					break;
				
			} 
			// console.log(star)
			return star;
		}
	},
	created() {//监听页面加载，其参数为上个页面传递的数据，参数类型为Object
			// this.getLoginMsg()
	},
	methods:{
		getClickPer:function(e){
			this.pageId=e.currentTarget.id*1;
			switch (this.pageId) {
				case 0:
					navigateTo('/pages/index/index',null);
					break;
				case 1:
					navigateTo('/pages/allProducts/allProducts',null);
					break;
				case 2:
					navigateTo('/pages/commodityClassification/commodityClassification',null);
					break;
				case 3:
					navigateTo('/pages/shopMembers/shopMembers',null);
					break;
			} 
		}
		
	}
}
